import { Float, Text, Html, OrbitControls, PivotControls, DragControls } from "@react-three/drei"
import { MeshReflectorMaterial } from "@react-three/drei"
import { useRef, useState } from "react"
// import { DragControls } from "three-stdlib"

export default function Experience()
{
    const cube = useRef()
    const sphere = useRef()
    const props={
        sphere_x:-2,
        sphere_z:0,
        cube_x:2,
        cube_z:0
    }
    const [shinySphere, setShinySphere] = useState(false)
    const [shinyCube, setShinyCube] = useState(false)

    return <>
    
        <OrbitControls 
            makeDefault 
            minPolarAngle={Math.PI / 4}
            maxPolarAngle={Math.PI * (1 / 2 - 1 / 20)}
            maxAzimuthAngle={0}
            minAzimuthAngle={-Math.PI /2}   
            enableZoom={false}
        />

        <directionalLight position={ [ 1, 2, 3 ] } intensity={ 3 } />
        <ambientLight intensity={ 1.5 } />

        {/* <PivotControls 
            anchor={[0,0,0]} // based on the location in the relevant object 
            depthTest = {false}
            lineWidth= { 2 }
            axisColors={['#9381ff', '#ff4d6d', '#7ae582']}
            scale={1}
        > */}
         <DragControls 
            axisLock="y"
            dragLimits={[[-props.sphere_x-4,-props.sphere_x+4],,[-props.cube_z-4,-props.cube_z+4]]}
            >
            <mesh 
                ref={sphere} 
                position={ [props.sphere_x, 0, props.sphere_z ]}
                onPointerEnter={() => setShinySphere(true)}
                onPointerLeave={() => setShinySphere(false)}
            >
                <sphereGeometry />
                <meshStandardMaterial color={shinySphere? "LightSalmon":"orange"} />
                <Html 
                    position={[1,1,0]}
                    wrapperClass="label"
                    center //center the html around the position
                    distanceFactor={9}
                    occlude = { [ sphere, cube ] }
                >
                    Thats a sphere 
                </Html>
            </mesh>
        </DragControls>


        <DragControls 
            axisLock="y"
            dragLimits={[[-props.cube_x-4,-props.cube_x+4],,[-props.cube_z-4,-props.cube_z+4]]}           
        >
            <mesh 
                ref={cube} 
                position={ [props.cube_x,0,props.cube_z] } 
                scale={ 1.5 }
                onPointerEnter={() => setShinyCube(true)}
                onPointerLeave={() => setShinyCube(false)}
            >
                <boxGeometry />
                <meshStandardMaterial color={shinyCube? "MediumVioletRed": "mediumpurple"} />
            </mesh>
        </DragControls>
        

        {/* Walls and floor */}
     
        <mesh position-y={ - 1 } rotation-x={ - Math.PI * 0.5 } scale={ 10 }>
            <planeGeometry />
            <MeshReflectorMaterial
            resolution={512}
            blur = {[1000,1000]}
            mixBlur={1}
            mirror={0.75}
            color="greenyellow"/>
        </mesh>

        <mesh position={ [0,1.5,- 5] } rotation-x={ - Math.PI * 0 } scale={ [10,5,1] }>
            <planeGeometry />
            <MeshReflectorMaterial
            resolution={212}
            blur = {[1000,1000]}
            mixBlur={1}
            // mirror={0.75}
            color="CornflowerBlue"/>
        </mesh>

        <mesh position={ [5,1.5,0] } rotation-y={ -Math.PI * 0.5 } scale={ [10,5,1] }>
            <planeGeometry />
            <MeshReflectorMaterial
            resolution={212}
            blur = {[1000,1000]}
            mixBlur={1}
            // mirror={0.75}
            color="CornflowerBlue"/>
        </mesh>
       
        <Float
        speed={5}
        floatIntensity={2}>
        <Text
            fontSize={1}
            color="salmon"
            position={[0,2,-4]}
            maxWidth={2}
            textAlign="center"
        >
            Concept 
            {/* <meshNormalMaterial/> - depends on the orientation of the camera */}
        </Text>
        </Float>


    </>
}